import { Component, h, Prop, Element } from '@stencil/core';

@Component({
  tag: 'ks-cart-tile',
})
export class CartTile {
    @Element() root: HTMLElement;

    @Prop() name: string;
    @Prop() color: string;
    @Prop() logo: string;
    @Prop() price: string;

    componentDidLoad() {
        this.root.style.display = "block";
    }

    render() {
        const mutedColor = "#eeeeee";

        const titleStyle = {
            backgroundColor: this.color,
            color: "white", 
            height: "50px"
        }

        const priceStyle = {
            fontSize: "20px",
            padding: "5px 10px"
        }

        const infoStyle = {
            backgroundColor: mutedColor,
            msFlex: "1 0 auto",
            padding: "8px", 
            fontSize: "16px",
            borderTop: "1px solid rgba(0, 0, 0, 0.1)",
            transition: "background-color 0.1s ease-in, color 0.1s ease-in"
        }

        return(
            <div class="uk-height-1-1 uk-flex uk-flex-column">
                {this.price ? 
                    <div class="uk-flex uk-flex-middle uk-flex-between" style={titleStyle}>
                        <div class="uk-flex-auto">
                            <ks-image src={this.logo} alt={this.name} width="130" height="40"></ks-image>
                        </div>
                        <div class="ks-text-decorated uk-text-right" style={priceStyle}>
                            {this.price}
                        </div>
                    </div>
                :
                    <div class="uk-flex uk-flex-middle uk-flex-center" style={titleStyle}>
                        <ks-image src={this.logo} alt={this.name} width="140" height="40"></ks-image>
                    </div>
                }

                <div class="uk-flex-1 uk-flex uk-flex-middle" style={infoStyle}>
                    <div class="uk-width-1-1 uk-padding-small uk-text-center">
                        <slot/>
                    </div>

                </div>
            </div>
        );
    }
}